* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-item {
  display: flex;
  align-items: center;
  justify-content: start;
  height: auto;
  column-gap: 10px;
}
.country-name {
  font-size: 15px;
  margin: 5;
}
.country-list {
  padding-left: 10px;
}
.country-info {
  padding-left: 10px;
}
.country-title {
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: start;
  column-gap: 10px;
}
